import React, { PureComponent } from 'react'
import kline from '../../../../klineweb/src/klinecharts'
import Select from './Select'

class KLine extends PureComponent {
    constructor (props) {
        super(props)
        this.chart = null
    }

    componentDidMount () {
        this.chart = kline.init(document.getElementById('kline'))
        window.onresize = () => {
            this.chart.resize()
        }
        this.chart.setDataList(this.getData())
    }

    getData () {
        let dataList = []
        let timestamp = new Date().getTime() - 12 * 24 * 3600
        let baseValue = Math.random() * 12000
        let prices = []
        for (let i = 0; i < 10000; i++) {
            baseValue = baseValue + Math.random() * 20 - 10
            for (let j = 0; j < 4; j++) {
                prices[j] = (Math.random() - 0.5) * 12 + baseValue
            }
            prices.sort()
            let openIdx = +Math.round(Math.random() * 3).toFixed(0)
            let closeIdx = +Math.round(Math.random() * 2).toFixed(0)
            if (closeIdx === openIdx) {
                closeIdx++
            }
            let volume = Math.random() * 100

            timestamp += 60 * 1000

            let kLineModel = {
                open: prices[openIdx],
                low: prices[0],
                high: prices[3],
                close: prices[closeIdx],
                volume,
                timestamp,
                turnover: prices[closeIdx] * volume
            }
            dataList.push(kLineModel)
        }
        return dataList
    }

    render () {

        return (
            <div style={{height: '100%', overflow: 'auto', minWidth: 1200}}>
                <div id="kline" style={{ height: 500 }} />
                <Select
                    label="main indicator："
                    datas={['NO', 'MA', 'BOLL', 'SAR']}
                    onSelected={(indicatorType) => {this.chart.setMainIndicatorType(indicatorType)}}
                />
                <Select
                    label="sub indicator："
                    datas={[
                        'NO', 'MA', 'VOL', 'MACD',
                        'BOLL', 'KDJ', 'KD', 'RSI',
                        'BIAS', 'BRAR', 'CCI', 'DMI',
                        'CR', 'PSY', 'DMA', 'TRIX',
                        'OBV', 'VR', 'WR', 'MTM',
                        'EMV', 'SAR'
                    ]}
                    onSelected={(indicatorType) => {this.chart.setSubIndicatorType(indicatorType)}}
                />
                <Select
                    style={{display: 'inline-block'}}
                    label="main chart type："
                    datas={[
                        'candle', 'time_line'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({candle: {chartType: type }})
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="candle style："
                    datas={[
                        'solid', 'stroke', 'increasing_stroke', 'decreasing_stroke', 'ohlc'
                    ]}
                    onSelected={
                        (style) => {
                            this.chart.setConfig({candle: {candleChart: {candleStyle: style}} })
                        }
                    }
                />
                <br/>
                <Select
                    style={{display: 'inline-block'}}
                    label="highest price mark："
                    datas={[
                        'show', 'hide'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({candle: {highestPriceMark: {display: type === 'show'}} })
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="lowest price mark："
                    datas={[
                        'show', 'hide'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({candle: {lowestPriceMark: {display: type === 'show'}} })
                        }
                    }
                />
                <br/>
                <Select
                    style={{display: 'inline-block'}}
                    label="last price mark："
                    datas={[
                        'show', 'hide'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({candle: {lastPriceMark: {display: type === 'show'}} })
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="last price mark line style："
                    datas={[
                        'dash', 'solid'
                    ]}
                    onSelected={
                        (style) => {
                            this.chart.setConfig({candle: {lastPriceMark: {lineStyle: style}} })
                        }
                    }
                />
                <br/>
                <Select
                    style={{display: 'inline-block'}}
                    label="x axis separator line："
                    datas={[
                        'hide', 'show'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({xAxis: {separatorLine: {display: type === 'show'}} })
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="x axis separator line style："
                    datas={[
                        'dash', 'solid'
                    ]}
                    onSelected={
                        (style) => {
                            this.chart.setConfig({xAxis: {separatorLine: {style: style}} })
                        }
                    }
                />
                <br/>
                <Select
                    style={{display: 'inline-block'}}
                    label="y axis separator line："
                    datas={[
                        'hide', 'show'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({yAxis: {separatorLine: {display: type === 'show'}} })
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="y axis separator line style："
                    datas={[
                        'dash', 'solid'
                    ]}
                    onSelected={
                        (style) => {
                            this.chart.setConfig({yAxis: {separatorLine: {style: style}} })
                        }
                    }
                />

                <br/>
                <Select
                    style={{display: 'inline-block'}}
                    label="y axis position："
                    datas={[
                        'left', 'right'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({yAxis: {position: type} })
                        }
                    }
                />
                <Select
                    style={{display: 'inline-block', marginLeft: 50}}
                    label="y axis text position："
                    datas={[
                        'inside', 'outside'
                    ]}
                    onSelected={
                        (type) => {
                            this.chart.setConfig({yAxis: {tickText: {position: type}} })
                        }
                    }
                />
                <br/>
                <br/>
                <br/>
                <br/>
            </div>
        )
    }
}

export default KLine
