import React, { PureComponent } from 'react'
import './select.css'
class Select extends PureComponent {
    constructor (props) {
        super(props)
        this.state = {
            selectedIndex: 0
        }
    }

    onItemClick (index) {
        this.props.onSelected(this.props.datas[index])
        this.setState({
            selectedIndex: index
        })

    }
    render () {
        return(
            <div style={{marginTop: 10, ...this.props.style}}>
                <span className="select-label">{this.props.label}</span>
                {
                    this.props.datas.map((data, index) => <span onClick={() => { this.onItemClick(index) }} className={`item ${index === this.state.selectedIndex ? 'item-active' : ''}`}>{data}</span>)
                }
            </div>
        )
    }
}

Select.defaultProps = {
    style: {},
    label: '',
    datas: [],
    onSelected: () => {}
}

export default Select